import Vue from 'vue';
import axios from 'axios';
import { Message, MessageBox } from 'element-ui';
import store from '../store';
import { LOGOUT } from '../store/types';
import {
  API_HOST,
  API_BASE_URL,
  API_REQUEST_TIMEOUT,
  AUTH_KEY,
  LOGIN_URL,
} from '../config';

import './mock'; // mock 数据

axios.defaults.baseURL = API_HOST + API_BASE_URL;
axios.defaults.timeout = API_REQUEST_TIMEOUT;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.headers.common[AUTH_KEY] = store.state.token;

// request 拦截
axios.interceptors.request.use((config) => {
  if (!config.headers[AUTH_KEY] && store.state.token) {
    axios.defaults.headers.common[AUTH_KEY] = store.state.token;
    config.headers[AUTH_KEY] = store.state.token;
  }
  return config;
}, (error) => {
  Message.error({
    message: '加载超时',
  });
  Promise.reject(error);
});
const responseStatusHandler = {
  // 可能是token过期，清除它
  401: () => {
    MessageBox.alert('登录状态信息过期,请重新登录', {
      type: 'error',
      showClose: false,
      closeOnPressEscape: false,
      confirmButtonText: '重新登录',
      callback: () => {
        window.location.href = LOGIN_URL('/');
      },
    });
  },
  402: () => {
    store.dispatch(LOGOUT);
  },
};
// respone 拦截
axios.interceptors.response.use(
  response => response,
  (error) => { // 默认除了2XX之外的都是错误的，就会走这里
    if (error.response) {
      const { status } = error.response;
      const func = responseStatusHandler[status];
      if (func) func();
      else {
        Message.error({
          title: error.status,
          message: error.response.data.message || error.message,
        });
      }
    }
    return Promise.reject(error);
  },
);

Vue.prototype.$http = axios;
export default axios;
