import store from '@/store';
import { REPLACE } from '@/store/modules/breadcrumb';

/**
 * 更新面包屑
 * @param {Object} route
 */
const updateBreadcrumb = (to) => {
  const breadcrumb = [];
  if (to.matched.length) {
    to.matched.forEach(route => breadcrumb.push({
      path: route.path || route.meta.original,
      name: route.name,
      title: route.meta.title || route.name,
    }));
  }
  store.commit(REPLACE, breadcrumb);
};

export default updateBreadcrumb;
