/**
 * 开发/生产标识
 * 生产环境: production
 * 开发环境: development
 */
export const ENV = process.env.NODE_ENV;
/**
 * 当前域名
 */
export const HOST = ENV === 'development' ? '' : '';
/**
 * 默认Title
 */
export const DEFAULT_TITLE = '仪表盘';
/**
 * 项目名
 */
export const PROJECT_NAME = '大屏仪表盘';
/**
 * 项目编号
 */
export const PROJECT_ID = '';
/**
 * 基本URL
 */
export const BASE_URL = '/';
/**
 * API域名
 */
export const API_HOST = window.location.origin;
/**
 * API基本URL
 */
export const API_BASE_URL = '/';
/**
 * 请求限时
 */
export const API_REQUEST_TIMEOUT = 30000;
/**
 * 身份识别KEY，接口请求时附带到Header
 */
export const AUTH_KEY = 'Authorization';
/**
 * 登录后回调的路由路径
 */
export const AUTH_PATH = `${BASE_URL}auth`;
/**
 * Token获取接口地址
 * code query
 */
export const AUTH_URL = '/user/accesstoken';
/**
 * 退出登录接口
 */
export const LOGOUT_URL_API = '/user/logout';
/**
 * PUP退出登录URL
 */
export const LOGOUT_URL = '/user-asserver/logout?redirectUrl=';
/**
 * 登录基本URL
 */
export const LOGIN_BASE_URL = '/login';
/**
 * (子)系统PUP登录标识，跳转至登录时附带
 */
export const CLIENT_ID = '9d5d629f77904a1ab9f5a40c7550379e';
/**
 * 登录URL
 * @param {string} state 授权成功后回调地址
 */
export const LOGIN_URL = (state) => {
  const data = {
    client_id: CLIENT_ID,
    response_type: 'code',
    state: encodeURIComponent(state),
    redirect_uri: encodeURIComponent(`${window.location.origin}${window.location.pathname}#${AUTH_PATH}`),
  };
  const params = Object.keys(data).map(k => `${k}=${data[k]}`).join('&');
  return `${LOGIN_BASE_URL}?${params}`;
};
