import Vue from 'vue';

import {
  SET_ATTR,
  TOGGLE_SIDEBAR,
} from './types';

export default {
  [SET_ATTR](state, payload) {
    Object.keys(payload).forEach((k) => {
      Vue.set(state, k, payload[k]);
    });
  },
  [TOGGLE_SIDEBAR](state) {
    state.sidebar.opened = !state.sidebar.opened;
  },
};
