import axios from 'axios';
import $api from '../service/api';
import { LOGOUT_URL_API, LOGOUT_URL } from '../config';
import {
  SET_ATTR,
  LOGIN,
  LOGOUT,
  INIT,
  FETCH_USERINFO,
  FETCH_PERMISSION,
  FETCH_USERROLE,
} from './types';

export default {
  [INIT]({ commit, dispatch }) {
    return Promise.all([
      // 已从jwt中读取
      // dispatch(FETCH_USERINFO),
      // 加载权限
      dispatch(FETCH_PERMISSION),
      // 加载角色信息
      dispatch(FETCH_USERROLE),
    ])
      .then(() => {
        commit(SET_ATTR, {
          inited: true,
        });
      });
  },
  [LOGIN]({ commit, dispatch }, payload) {
    return new Promise((resolve, reject) => {
      commit(SET_ATTR, payload);
      dispatch(INIT).then(resolve).catch(reject);
    });
  },
  [LOGOUT]({ commit }) {
    axios.put(LOGOUT_URL_API).then(() => {
      commit(SET_ATTR, {
        token: '',
        userinfo: {},
      });
      // LOGOUT_URL
      window.location.href = `${LOGOUT_URL}${window.location.href}`;
    });
  },
  /**
   * 获取用户信息
   */
  [FETCH_USERINFO]({ commit }) {
    return new Promise((resolve, reject) => {
      $api.userinfo().then(({ data }) => {
        if (data.status !== 'OK') {
          reject(data);
          return;
        }
        commit(SET_ATTR, {
          userinfo: data.data,
        });
        resolve();
      }, reject);
    });
  },
  /**
   * 获取用户权限
   */
  [FETCH_PERMISSION]({ state, commit }) {
    return new Promise((resolve, reject) => {
      $api.userPermission({
        userCode: state.userinfo.user_code,
      }).then(({ data }) => {
        if (data.status !== 'OK') {
          reject(data);
          return;
        }
        const permission = data.data.map(v => v.objCode);
        commit(SET_ATTR, {
          permission,
        });
        resolve();
      }, reject);
    });
  },
  /**
   * 获取用户角色信息
   */
  async [FETCH_USERROLE]({ state, commit }) {
    try {
      const { data } = await $api.userRole({
        userCode: state.userinfo.user_code,
      });
      if (data && data.status === 'OK') {
        commit(SET_ATTR, {
          userinfo: Object.assign({}, state.userinfo, data.data),
        });
        return Promise.resolve();
      }
      throw new Error('invlid');
    } catch (e) {
      return Promise.reject(e);
    }
  },
};
