import { MessageBox, Loading } from 'element-ui';
import store from '@/store';
import { INIT } from '@/store/types';
import { LOGIN_URL } from '@/config';
import router from './instance';
import updateBreadcrumb from './breadcrumb';
import updateDocumentTitle from './documentTitle';

router.beforeEach((to, from, next) => {
  // 请确保不需要授权的路由设置了requireAuth===false
  if (to.matched.length && to.matched.some(route => route.meta.requireAuth !== false)) {
    if (store.state.token) {
      if (store.state.inited) {
        next();
      } else {
        const $loading = Loading.service({
          fullscreen: true,
          lock: true,
          text: '加载中',
        });
        store.dispatch(INIT).then(() => {
          $loading.close();
          next();
        }).catch(() => {
          MessageBox.alert('登录状态信息过期,请重新登录', {
            type: 'error',
            showClose: false,
            closeOnPressEscape: false,
            confirmButtonText: '重新登录',
            callback: () => {
              next(LOGIN_URL(to.fullPath));
            },
          });
          $loading.close();
          next(false);
        });
      }
    } else {
      next(LOGIN_URL(to.fullPath));
      return;
    }
  } else {
    next();
  }
  updateDocumentTitle(to);
  updateBreadcrumb(to);
});
router.beforeResolve((to, from, next) => {
  next();
});
export default router;
